import { FC } from 'react';
import { TwBaseProps } from '../../types';
import {
  DefaultProps,
  Center as MantineCenter,
  CenterProps as MantineCenterProps,
} from '@mantine/core';

export interface CenterProps extends TwBaseProps, Omit<MantineCenterProps, keyof DefaultProps> {}

// NOTE:
//  MantineCenter doesn't work with styled from emotion,
//  since styled needs a component that accepts "children" props,
//  but Mantine omits the possibility of a component that accepts
//  children...
const Root: FC<MantineCenterProps> = (props) => <MantineCenter {...props} />;

// TODO: See what to do about forwarding refs here
export const Center: FC<CenterProps> = (props) => {
  return <Root {...props} />;
};
